@use "../mixins";

* {
	box-sizing: border-box;
	scroll-behavior: smooth;
}

:after,
:before {
	box-sizing: inherit;
}

html,
body {
	width: 100%;
	min-height: 100vh;
	background: #fff1cc;

	-webkit-tap-highlight-color: transparent;
	scrollbar-gutter: stable;

	font-family: 'Roboto', system-ui, sans-serif;
	color: #2C2C2C;

	padding: 0;
	margin: 0;
}

/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
	-webkit-appearance: none;
	margin: 0;
}

/* Firefox */
input[type=number] {
	-moz-appearance: textfield;
}

button {
	background: none;
	border: none;
	cursor: pointer;

	margin: 0;
	padding: 0;
}

figure {
	display: flex;

	padding: 0;
	margin: 0;
}

img {
	vertical-align: top;
}

a {
	color: inherit;
	text-decoration: none;

	padding: 0;
	margin: 0;
}

ul,
ol {
	list-style: none;

	padding: 0;
	margin: 0;
}

p {
	padding: 0;
	margin: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	padding: 0;
	margin: 0;
}

input,
textarea,
select {
	font-style: inherit;
}

#root {
	display: flex;
	flex-direction: column;
}

.page-header {
	flex-shrink: 0;
}

.page-content {
	flex-grow: 1;
}

.page-footer {
	flex-shrink: 0;
}

.base-container {
	width: 95vw;
	max-width: 1240px;

	margin: 0 auto;
}

.section-title {
	font-size: 25px;
	font-weight: 500;
	letter-spacing: 0.32px;
}

.visually-hidden {
	position: absolute;

	width: 1px;
	height: 1px;
	border: 0;
	padding: 0;
	clip-path: inset(100%);
	clip: rect(0 0 0 0);
	overflow: hidden;

	white-space: nowrap;

	margin: -1px;
}